import listCell from '@/components/mix-list-cell';
import uniBadge from "@/components/uni-badge/uni-badge.vue";
import uniPopup from "@/components/uni-popup/uni-popup.vue"




import shareByWx from '@/components/share/shareByWx.vue'







import {
	mapState,
	mapMutations
} from 'vuex';
let startY = 0,
	moveY = 0,
	pageAtTop = true;
export default {
	components: {
		listCell,
		uniBadge,
		uniPopup,



		

		shareByWx,

		



		



	},
	data() {
		return {
			type: '',
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			toPayOrderCount:0,
			toConfirmOrderCount:0
		}
	},
	onLoad() {},
	onShareAppMessage(res) {
	    return {
	      title: '我是'+this.userInfo.name+',注册会员,立享佣金!',
		  imageUrl:this.userInfo.photoUrl,
	      path: '/pages/public/register?id=' + this.userInfo.userUuid
	    }
	 },




















	computed: {
		...mapState(['hasLogin', 'userInfo', 'footPrint', 'applicationConfig']),
		shareHref() {
			let pages = getCurrentPages()
			let page = pages[pages.length - 1]

			return this.$api.request.apiBaseUrl.replace("/b2c/rest/","") + '/#/pages/public/register?id=' + this.userInfo.userUuid;

		



		}
	},
	methods: {
		...mapMutations(['login']),
		togglePopup(type, open) {
			switch (type) {
				case 'top':
					this.content = '顶部弹出 popup'
					break
		
				case 'bottom':
					this.content = '底部弹出 popup'
					break
				case 'center':
					this.content = '居中弹出 popup'
					break
			}
			this.type = type
			this.$nextTick(() => {
				this.$refs['show' + open].open()
			})
		},
		closeShare() {
			this.$refs.showshare.close();
		},
		inquiryOrderTotal(orderStatus) {
			let searchOptions = {
				keyArray: ['USER', 'ORDERSTATUS'],
				userUuid: this.userInfo.userUuid,
				orderStatus: orderStatus
			};
			this.$api.request.searchOrderTotal(searchOptions, res => {
				if (res.body.status.statusCode === '0') {
					var total = res.body.data.total;
					if(orderStatus == '0'){
						this.toPayOrderCount = total;
					}else if(orderStatus == '2'){
						this.toConfirmOrderCount = total;
					}
				} else {
					console.log(res.body.status.errorDesc);
				}
			},true);
		},
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navTo(url) {
			if (!this.hasLogin) {
				url = '/pages/public/login';
			}
			uni.navigateTo({
				url
			})
		},

		/**
		 *  会员卡下拉和回弹
		 *  1.关闭bounce避免ios端下拉冲突
		 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
		 *    transition设置0.1秒延迟，让css来过渡这段空窗期
		 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
		 */
		coverTouchstart(e) {
			if (pageAtTop === false) {
				return;
			}
			this.coverTransition = 'transform .1s linear';
			startY = e.touches[0].clientY;
		},
		coverTouchmove(e) {
			moveY = e.touches[0].clientY;
			let moveDistance = moveY - startY;
			if (moveDistance < 0) {
				this.moving = false;
				return;
			}
			this.moving = true;
			if (moveDistance >= 80 && moveDistance < 100) {
				moveDistance = 80;
			}

			if (moveDistance > 0 && moveDistance <= 80) {
				this.coverTransform = `translateY(${moveDistance}px)`;
			}
		},
		coverTouchend() {
			if (this.moving === false) {
				return;
			}
			this.moving = false;
			this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
			this.coverTransform = 'translateY(0px)';
		}
	},
	onShow() {
		//同步用户信息
		if (this.hasLogin) {
			this.$api.request.userInfo({
				userUuid: this.userInfo.userUuid
			}, res => {
				if (res.body.status.statusCode === '0') {
					this.login(res.body.data);
				}
			});
			//获取待付款和待收货订单数量(0-待付款, 1-待发货， 2-待收货)
			this.inquiryOrderTotal('0');	
			this.inquiryOrderTotal('2');
		}
	}
}
